<template>
  <div id="assets">
    <section class="section">
      <h1 class="is-size-4 has-text-left">Rental Movies</h1>
      <div class="horizontal-scroll-wrapper has-text-left">
        <router-link
          v-for="m in movieRentals"
          :to="`/assets/${m.id}`"
          :key="m.id"
        >
          <img
            v-if="m.images"
            :src="filterImage(m.images)[0].url + '?im=Resize,height=200'"
          />
        </router-link>
      </div>
      <h1 class="is-size-4 has-text-left">Wishlist - Movies</h1>
      <div class="horizontal-scroll-wrapper has-text-left">
        <router-link
          v-for="m in wishlist.movies"
          :to="`/assets/${m.id}`"
          :key="m.id"
        >
          <img :src="filterImage(m.images)[0].url + '?im=Resize,height=200'" />
        </router-link>
      </div>
      <h1 class="is-size-4 has-text-left">Wishlist - TV Shows</h1>
      <div class="horizontal-scroll-wrapper has-text-left">
        <router-link
          v-for="m in wishlist.series"
          :to="`/assets/${m.id}`"
          :key="m.id"
        >
          <img :src="filterImage(m.images)[0].url + '?im=Resize,height=200'" />
        </router-link>
      </div>

      <router-view></router-view>
    </section>
  </div>
</template>
<script>
export default {
  name: "Assets",
  data() {
    return {
      wishlist: {},
      librarylist: {},
      movieRentals: [],
    };
  },
  computed: {},
  mounted() {
    let authData = JSON.parse(localStorage.getItem("authData"));
    this.getLibraryList(authData).then((res) => {
      this.librarylist = res;
      this.movieRentals = res.movies.rentals;

      for (let i = 0; i < this.movieRentals.length; i++) {
        let asset = {};
        this.getAsset(this.movieRentals[i].id, authData).then((a) => {
          asset = {
            ...this.movieRentals[i],
            images: a[0].images,
          };
          this.$set(this.movieRentals, i, asset);
        });
      }
    });

    // res.movies.rentals[i].images = a[0].images;

    this.getWishList(authData).then((res) => {
      this.wishlist = res;
    });
  },
  $route: {
    immediate: true,
    handler: function (newVal) {
      this.showModal = newVal.meta && newVal.meta.showModal;
    },
  },
  methods: {
    filterImage(images) {
      let i = images.filter((c) => c.type === "SLICK");
      return i;
    },
    getAsset(assetId, authData) {
      return new Promise((resolve, reject) => {
        var AuthHeaders = new Headers();
        AuthHeaders.append("Authorization", `Bearer ${authData.access_token}`);
        AuthHeaders.append("Content-Type", "application/json");

        var requestOptions = {
          method: "GET",
          headers: AuthHeaders,
          redirect: "follow",
        };
        fetch(
          `https://hfozhlnmfh.execute-api.ap-southeast-2.amazonaws.com/v1/ttvbo/assets/${assetId}?attributes=*&uuid=${authData.uuid}`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    getWishList(authData) {
      return new Promise((resolve, reject) => {
        var AuthHeaders = new Headers();
        AuthHeaders.append("Authorization", `Bearer ${authData.access_token}`);
        AuthHeaders.append("Content-Type", "application/json");

        var requestOptions = {
          method: "GET",
          headers: AuthHeaders,
          redirect: "follow",
        };
        fetch(
          `https://hfozhlnmfh.execute-api.ap-southeast-2.amazonaws.com/v1/ttvbo/users/${authData.uuid}/wishlist/assets?deviceType=telstratv`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    getLibraryList(authData) {
      return new Promise((resolve, reject) => {
        var AuthHeaders = new Headers();
        AuthHeaders.append("Authorization", `Bearer ${authData.access_token}`);
        AuthHeaders.append("Content-Type", "application/json");
        //{"movies":{"rentals":[],"purchases":[]},"series":{"rentals":[],"purchases":[]}}
        var requestOptions = {
          method: "GET",
          headers: AuthHeaders,
          redirect: "follow",
        };
        fetch(
          `https://hfozhlnmfh.execute-api.ap-southeast-2.amazonaws.com/v1/ttvbo/users/${authData.uuid}/library?attributes=id,displayTitle,type,availability,videos,parentSeriesId,parentSeasonId,seasons.id,seasons.displayTitle,seasons.type,seasons.availability,seasons.videos,seasons.parentSeriesId`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
  },
};
</script>

